function Users(props) {
  const usersKey = props.users;
  const ArraySum = function(ind1, ind2) {
    return ind1.map(function(item, idx) {
      return Object.keys(item).reduce(function(obj, key) {
        obj[key] = key === "name" ? item[key] : item[key] + ind2[idx][key];
        return obj;
      }, {});
    });
  };
  const values = usersKey.map(el => Data[el.name]).reduce(ArraySum);

  return <UsersChart data={values} xKey="name" />;
}

export default Users;
