import React from "react";
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend
} from "recharts";

const colors = ["red", "green", "blue", "orange", "darkviolet", "black"];

const UsersChart = ({ data, xKey }) => {
  const barsKeys = Object.keys(data[0]).filter(k => k !== xKey);
  return (
    <BarChart
      width={600}
      height={300}
      data={data}
      margin={{ top: 20, right: 30, left: 20, bottom: 5 }}
    >
      <CartesianGrid strokeDasharray="3 3" />
      <XAxis dataKey={xKey} />
      <YAxis />
      <Tooltip />
      <Legend />
      {barsKeys.map((key, i) => (
        <Bar dataKey={key} stackId="a" fill={colors[i]} />
      ))}
    </BarChart>
  );
};

export default UsersChart;
